.qualidade{
    margin: 60px auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.qualidade-item{
    flex: 1 1 300px;
    margin: 20px 10px;
    /* max-width: 300px; */
}
.qualidade-item h2{
    font-size: 2.25rem;
    color: #a4a4a4;
    margin-bottom: 10px;
}
.qualidade-item h2::before{
    content: '';
    display: inline-block;
    width: 8px;
    height: 30px;
    margin-right: 10px;
    background: #2c70ff;
}
.qualidade-item:nth-of-type(3n+1) h2::before{
    background: #ae81ff;
}
.qualidade-item:nth-of-type(3n+2) h2::before{
    background: #f9265e;
}
.qualidade-item:nth-of-type(3n+3) h2::before{
    background: #66d9eb;
}

.qualidade-item p{
    font-size: 1.25rem;
    line-height: 1.4;
    color: #7c7c7c;
}

@media(max-width:592px){
    .qualidade-item{
        margin: 20px auto;
        padding: 0 10px;
    }
}